.modal {
  font-family: "Times New Roman", Times, serif;
  position: absolute;
  top: 0;
  left: 0;
  color: white;
  z-index: 200;
  //   transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.9);
  font-size: clamp(0.6rem, 2vw, 1rem);
  padding: 1rem;
  width: 100%;
  height: auto;
  border-radius: 15px;
}
